import { connect } from 'react-redux';
import PasswordComponent from '../components/Password';
import { fetchUDData } from '../../../../Redux/UserDetails/UDActions';
import { sendOTP, changePassword } from '../../../../Redux/ChangePassword/CPActions';

const mapStateToProps = state => {
  // console.log("printing from mydrive.js");
  // console.log(state.MyDrive);
  return{...state.UserDetails, ...state.ChangePassword}
}

const mapDispatchToProps = dispatch => {
	return {
			// fetchData: () => dispatch(fetchSetting_Data()),
			fetchUserData: () => dispatch(fetchUDData()),
      sendOTP: (params) => dispatch(sendOTP(params)),
      changePassword: (params) => dispatch(changePassword(params))
			// fetchUserData: () => fetchUDData()
	}
	}


export default connect(
  mapStateToProps,
  mapDispatchToProps
  )(PasswordComponent);