                                <input name="otp4" class="checkCPotpEnter mobbox2 f1 border_black1" type="text" id="fourthOTPcharmobile" maxlength="1" onChange={this.onOTPChange} onKeypress={(e) => chkInput(e)} onKeyup={(event) => move_toNext1(event, 'fourthOTPcharmobile', '')} autocomplete="off"/>
