function getMaskedMobile(mobile) {
    if(!mobile) return '';
    return "*".repeat(mobile.length - 4) + mobile.substr(mobile.length - 4);
}

function getMaskedEmail(email){
    if(!email) return '';
    return email.substr(0,1) + "*".repeat(email.indexOf('@')-1) + email.substr(email.indexOf('@'));
}
export {
    getMaskedMobile,
    getMaskedEmail
};